	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Auf die Palme","date":"21 Jun 2004","slug":"/2004-06-21-auf-die-palme/","tags":"Y2004"},"html":"<p>Our new cat Nena had a unique way of celebrating Midsummer this morning. She decided she wanted to be let outside at the crack of dawn, which at our latitude was 3:30 am. She's learned that she can wake us up by rustling through the potted palm in our bedroom. Very much annoyed, I brought her downstairs, threw her outside and went back to bed.</p>\n<p>My wife, however, couldn't get back to sleep and went downstairs. She must have let Nena back in, because at some point the cat was back upstairs rustling again through the palm. This time she was clumsy, toppling the palm with 20-liter pot and potting soil onto the floor with a crash. Needless to say, the night was now ruined, as well as the day. Tonight both the palm and the cat will be set outside.</p>\n<p><a href=\"http://dict.leo.org/?search=auf die palme\" title=\"LEO Results for auf die palme\">Auf die Palme bringen</a>: to drive (someone) crazy.</p>"},"next":{"fields":{"title":"Null Number","date":"19 Jun 2004","slug":"/2004-06-19-null-number/"}},"previous":{"fields":{"title":"New Religion","date":"21 Jun 2004","slug":"/2004-06-21-new-religion/"}}},{"node":{"fields":{"title":"Null Number","date":"19 Jun 2004","slug":"/2004-06-19-null-number/","tags":"Y2004"},"html":"<p><a href=\"http://news.bbc.co.uk/sport1/hi/football/euro_2004/3787541.stm\" title=\"BBC SPORT | Football | Euro 2004 | Latvia 0-0 Germany\">Latvia 0:0 Germany</a> After looking brilliant against Holland, Germany today just looked tired and frustrated. But look on the bright side. They have now doubled their point count of <a href=\"http://www.soccernet.com/euro2000/germany.html\">Euro 2000</a>.</p>"},"next":{"fields":{"title":"DSLed","date":"18 Jun 2004","slug":"/2004-06-18-dsled/"}},"previous":{"fields":{"title":"Auf die Palme","date":"21 Jun 2004","slug":"/2004-06-21-auf-die-palme/"}}},{"node":{"fields":{"title":"DSLed","date":"18 Jun 2004","slug":"/2004-06-18-dsled/","tags":"Y2004"},"html":"<p>Much to my surprise, our DSL connection was activated on schedule today and the splitter arrived with today's post. Alas, our ISDN NTBA and DSL splitter are in our basement washroom, where I have no ethernet cable, so we can surf from the washing machine but not from our desktop PCs. I've put our WLAN access point in our entryway under the coat rack, with a cable to the basement, so we can at least use our notebooks until I can cable things correctly.</p>"},"next":{"fields":{"title":"Where I Won't Be","date":"17 Jun 2004","slug":"/2004-06-17-where-i-wont-be/"}},"previous":{"fields":{"title":"Null Number","date":"19 Jun 2004","slug":"/2004-06-19-null-number/"}}}],"pathPrefix":"","first":false,"last":false,"index":463,"pageCount":1101}}

